import React from 'react'
import "./_style.scss";

interface IPhoneComp {
  iconName: string,
  isShowNumber: boolean
}

function PhoneComp(props: IPhoneComp) {
  const { iconName, isShowNumber } = props;
  return (
    <a
      href="tel:+998951235588"
      className='phone_link'
    >
      <img src={`/assets/icons/${iconName}.svg`} alt="tel" />
      {
        isShowNumber && (
          <strong className='title18_bold'>+998 95 123 55 88</strong>
        )
      }
    </a>
  )
}

export default PhoneComp