import { RightOutlined } from '@ant-design/icons';
import React, { Dispatch, SetStateAction, useEffect } from 'react'
import { Link } from 'react-router-dom';
import "./_style.scss";

interface IBurgerMenu {
  isOpen: boolean;
  setIsShowBurgerMenu: Dispatch<SetStateAction<boolean>>
}

const categorries = [

]

function BurgerMenu(props: IBurgerMenu) {

  const { isOpen, setIsShowBurgerMenu } = props;


  const openCity = (event: React.MouseEvent<Element, MouseEvent>, categoryName: string) => {
    let i, tabcontent, tablinks;
    tabcontent = document.getElementsByClassName("tabcontent")!;
    for (i = 0; i < tabcontent.length; i++) {
      const currentTabContent = tabcontent[i] as HTMLElement;
      currentTabContent.style["display"] = "none";
    }
    tablinks = document.getElementsByClassName("tablinks");
    for (i = 0; i < tablinks.length; i++) {
      tablinks[i].className = tablinks[i].className.replace(" active", "");
    }
    document.getElementById(categoryName)!.style.display = "block";
    event.currentTarget.className += " active";
  }

  // burger menu boshqa polya bosilganda yopilish logikasi

  useEffect(() => {
    const burgerMenu = document.getElementById("burger_menu")!;
    const openBtn = document.getElementById("caregory_open_btn")!;
    const cubcategoryLink = document.getElementsByClassName("subcategry_links")!;
    const tabLink = document.getElementsByClassName("tablinks")!;


    document.body.addEventListener("click", function (e: any) {
      const clickedPlace = e.target as Node;
      if (!(burgerMenu.contains(clickedPlace) || openBtn.contains(clickedPlace)) || (Array.from(cubcategoryLink).some(el => el.contains(clickedPlace)) || Array.from(tabLink).some(el => el.contains(clickedPlace)))) {
        setIsShowBurgerMenu(false);
      }
    });
  }, [setIsShowBurgerMenu]);


  return (
    <div id="burger_menu" className={`burger_menu_wrapper ${isOpen ? "active" : ""}`}>
      <div className="burger_menu">
        {/* menu tab */}
        <div className="tab">
          <ul>
            <li>
              <Link
                to="#"
                className="tablinks"
                onMouseOver={(e) => openCity(e, 'London')}
              >
                <img src="/assets/icons/phone.svg" alt="phone" />
                <span className="p18_regular">Телефоны, планшеты</span> <RightOutlined className='right_arrow' />
              </Link>
            </li>
            <li>
              <Link to="#"
                className="tablinks"
                onMouseOver={(e) => openCity(e, 'Paris')}
              >
                <img src="/assets/icons/phone.svg" alt="phone" />
                <span className="p18_regular">Телефоны, планшеты</span> <RightOutlined className='right_arrow' />
              </Link>
            </li>
            <li>
              <Link
                to="#"
                className="tablinks"
                onMouseOver={(e) => openCity(e, 'Tokyo')}
              >
                <img src="/assets/icons/phone.svg" alt="phone" />
                <span className="p18_regular">Телефоны, планшеты</span> <RightOutlined className='right_arrow' />
              </Link>
            </li>
          </ul>
        </div>
        {/* tab contents */}
        <div id="London" className="tabcontent">
          <h3 className="title24_bold">London</h3>
          <div className="tab_content_body">
            <div className="subcategry_links">
              <Link
                to="#"
                className="p18_regular subcategory_link"
              >
                Paris is the capital of France.
              </Link>
            </div>
            <div className="category_img">
              <img src="/assets/img/tp_link.png" alt="tplink" />
            </div>
          </div>
        </div>

        <div id="Paris" className="tabcontent">
          <h3 className="title24_bold">Paris</h3>
          <div className="tab_content_body">
            <div className="subcategry_links">
              <Link
                to="#"
                className="p18_regular subcategory_link"
              >
                Paris is the capital of France.
              </Link>
            </div>
            <div className="category_img">
              <img src="/assets/img/tp_link.png" alt="tplink" />
            </div>
          </div>
        </div>

        <div id="Tokyo" className="tabcontent">
          <h3 className="title24_bold">Tokyo</h3>
          <div className="tab_content_body">
            <div className="subcategry_links">
              <Link
                to="#"
                className="p18_regular subcategory_link"
              >
                Paris is the capital of France.
              </Link>
            </div>
            <div className="category_img">
              <img src="/assets/img/tp_link.png" alt="tplink" />
            </div>
          </div>
        </div>
      </div>
    </div >
  )
}

export default BurgerMenu